import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { InputNumber } from "primereact/inputnumber";
import { InputText } from "primereact/inputtext";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { productCategoryActions } from "../../store/productCategory/productCategorySlice";
import { IAppState } from "../../models/appState";
import { IProductCategory } from "../../models/productCategory";
import { v4 as uuidv4 } from "uuid";

const ProductCategoryEdit: React.FC = () => {
  const defaultItem: IProductCategory = {
    id: "",
    name: "",
    sortOrder: 0,
  };
  const navigateTo = "/productcategories";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);
  const [productCategory, isFetching, isError, productCategories] =
    useAppSelector(
      (state: IAppState) => [
        state.productCategories.productCategory,
        state.productCategories.isFetching,
        state.productCategories.isError,
        state.productCategories.productCategories,
      ],
      shallowEqual
    );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(productCategoryActions.getProductCategoryById(id));
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== productCategory?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(productCategory);
    }
  }, [params, item, productCategory, isFetching, isError]);

  const isNameValid = useCallback(
    (name: string) => {
      let result = true;
      if (item.id.length === 0) {
        if (
          productCategories.find(
            (x) => x.name?.toLowerCase() === name?.toLowerCase()
          )
        ) {
          result = false;
        }
      } else {
        if (
          productCategories.find(
            (x) =>
              x.name?.toLowerCase() === name?.toLowerCase() && x.id !== item.id
          )
        ) {
          result = false;
        }
      }

      return result;
    },
    [item.id, productCategories]
  );

  useEffect(() => {
    if (!dirty) {
      return;
    }

    let isValid = true;
    for (const [key, value] of Object.entries(item)) {
      if (key === "id") {
        continue;
      }

      if (typeof value === "string") {
        if (value?.trim().length === 0) {
          isValid = false;
          break;
        }
      } else if (typeof value === "number") {
        if ((value || 0) < 0) {
          isValid = false;
          break;
        }
      }
    }

    setValid(isValid && isNameValid(item.name || ""));
  }, [dirty, item, isNameValid]);

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    setItem({ ...item, [tname]: tvalue });
    const isValidName = isNameValid(tvalue);
    setErrors({
      ...errors,
      nameInvalid: !isValidName,
      [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
    });
  };

  const onInputNumberChange = (e: any) => {
    setDirty(true);
    const newValue: number = e.value === null ? -1 : e.value;
    setItem({ ...item, sortOrder: newValue });
    setErrors({ ...errors, sortOrder: newValue < 0 });
  };

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(productCategoryActions.updateProductCategory(item));
    setTimeout(() => {
      dispatch(productCategoryActions.getProductCategories());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Product Category: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Product Category</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="ProductCategoryName">
                  Product Category Name
                  <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="ProductCategoryName"
                  name="name"
                  maxLength={200}
                  value={item.name}
                  onChange={onChange}
                />
                {(errors as any)?.name && (
                  <Message
                    severity="error"
                    text="Product Category Name is required."
                  />
                )}
                {(errors as any)?.nameInvalid && (
                  <Message
                    severity="error"
                    text="Product Category Name already exists."
                  />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="SortOrder">
                  Sort Order <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputNumber
                  id="SortOrder"
                  name="sortOrder"
                  value={item.sortOrder}
                  maxFractionDigits={0}
                  onValueChange={(e) => onInputNumberChange(e)}
                />
                {(errors as any)?.sortOrder && (
                  <Message severity="error" text="Sort Order is required." />
                )}
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default ProductCategoryEdit;
